import React from 'react';
import Logo from '../images/logo.jpeg';

export type MessageCardAlert = {
    content: string;
    type: 'success' | 'error' | '';
};
interface Props {
    children: React.ReactNode;
    logoPosition?: 'left' | 'center' | 'none';
    alert?: MessageCardAlert;
}

const logoPositionClass = {
    left: '',
    center: 'mx-auto',
    none: 'hidden',
};

export const MessageCard: React.FC<Props> = ({ logoPosition = 'left', alert, children }) => {
    return (
        <div className="max-w-xl p-3.5 bg-white rounded shadow-md md:p-5">
            <img
                src={Logo}
                alt="Fantasy Franchise Logo"
                className={`object-contain h-24 mb-5 ${logoPositionClass[logoPosition]}`}
                height={96}
            />
            {alert?.content && (
                <p
                    className={`
                        w-full
                        text-center mb-4 py-4 -mt-1 rounded-sm
                        ${
                            alert.type === 'error'
                                ? 'text-danger-500 bg-red-100'
                                : 'text-primary-500 bg-primary-100'
                        }
                    `}
                >
                    {alert.content}
                </p>
            )}
            {children}
        </div>
    );
};
